@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Yanone+Kaffeesatz:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main_header {
  width: 100vw;
  min-height: 30vh;
  background-color: darkmagenta;
  color: darkorange;
  font-weight: 600;
  text-shadow: 5px 5px 5px darkred;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
div.App {
  font-family: "Yanone Kaffeesatz", sans-serif;
  width: 100vw;
  height: 100vh;
  background-color: rgb(255, 162, 0);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
/* 여기서부터 MainBBs CSS */
section.main_section {
  flex: 1 1;
}
div.bbsMain_list {
  margin: 0 auto;
  background-color: rgb(255, 162, 0);
  width: 60%;
}
table.bbs_list {
  text-align: center;
  width: 95%;
  margin: 20px auto;
  border-collapse: collapse;
  border-spacing: 0;
}
footer.footer_box {
  width: 100vw;
  text-align: center;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
div.main_navBox {
  height: 40px;
  background-color: darkorange;
}
ul.main_nav {
  list-style: none;
  display: flex;
  background-color: rgb(124, 0, 249);
  text-decoration: none;
  height: 4px;
  position: absolute;
  color: darkmagenta;
  text-shadow: 0.5px 0.5px 1px darkred;
}
ul.main_nav a {
  text-decoration: none;
  color: inherit;
  display: inline-block;
  padding: 0.5rem;
  font-weight: bold;
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
}
ul.main_nav a:hover {
  cursor: pointer;
  border-top: 5px solid darkorange;
  transition: 0.4s;
}

div.input_box {
  width: 100%;
  text-align: center;
}
div.input_box input:focus {
  background-color: cornsilk;
}

div.input_box input {
  margin: 3px auto;
  background-color: rgb(255, 162, 0);
  display: inline-block;
  border: 1px dotted black;
  padding: 4px;
  width: 50%;
  border-radius: 10px;
}
div.input_content input {
  padding: 3rem;
}
.btn_box {
  display: flex;
}
.btn_box button {
  width: 50%;
  margin: 20px auto;
  height: 30px;
  background-color: rgb(255, 162, 0);
  color: rgb(124, 0, 249);
  font-weight: bold;
  border: 4px double rgb(124, 0, 249);
}
.btn_box button:hover {
  cursor: pointer;
  transition: 0.3s;
  border: 6px double rgb(124, 0, 249);
  box-shadow: 0.5px 0.5px 10px rgb(124, 0, 249);
}

